nsition:all 0ms cubic-bezier(0.23, 1, 0.32, 1) 1ms;opacity:0;-webkit-transform:scale(0, 0);transform:scale(0, 0);-webkit-transform-origin:100% 0;transform-origin:100% 0}.header .sub-menu.active{transition-duration:250ms;opacity:1;-webkit-transform:scale(1, 1);transform:scale(1, 1)}@media screen and (min-width: 900px){.header .sub-menu.active{opacity:0}}.header .sub-menu .link{display:block;padding:0 1rem;line-height:2.25rem;color:inherit;text-decoration:none}.header .ellipsi{display:none;cursor:pointer;width:30px;font-size:25px;position:absolute;top:6px;right:12px}@media screen and (max-width: 900px){.header{left:0;z-index:20;padding:0;text-align:center}.header .contribute-link{display:none}.header .menu-button,.header .ellipsi{display:block}}@-webkit-keyframes inM{50%{-webkit-transform:rotate(0deg)}100%{-webkit-transform:rotate(45deg)}}@keyframes inM{50%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(45deg);transform:rotate(45deg)}}@-webkit-keyframes outM{50%{-webkit-transform:rotate(0deg)}100%{-webkit-transform:rotate(45deg)}}@keyframes outM{50%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(45deg);transform:rotate(45deg)}}@-webkit-keyframes inT{0%{-webkit-transform:translateY(0px) rotate(0deg)}50%{-webkit-transform:translateY(9px) rotate(0deg)}100%{-webkit-transform:translateY(9px) rotate(135deg)}}@keyframes inT{0%{-webkit-transform:translateY(0px) rotate(0deg);transform:translateY(0px) rotate(0deg)}50%{-webkit-transform:translateY(9px) rotate(0deg);transform:translateY(9px) rotate(0deg)}100%{-webkit-transform:translateY(9px) rotate(135deg);transform:translateY(9px) rotate(135deg)}}@-webkit-keyframes outT{0%{-webkit-transform:translateY(0px) rotate(0deg)}50%{-webkit-transform:translateY(9px) rotate(0deg)}100%{-webkit-transform:translateY(9px) rotate(135deg)}}@keyframes outT{0%{-webkit-transform:translateY(0px) rotate(0deg);transform:translateY(0px) rotate(0deg)}50%{-webkit-transform:transla