.sidebar{background:#fff;position:fixed;z-index:10;top:61px;left:0;bottom:0;box-shadow:5px 0 5px -2px rgba(156,39,176,0.15);overflow-x:hidden;overflow-y:hidden}.sidebar:hover{overflow-y:auto}.sidebar-inner{padding:40px 20px 60px 20px}.sidebar .search{height:30px;line-height:30px;box-sizing:border-box;padding:0 15px 0 30px;border:1px solid rgba(156,39,176,0.31);color:#2c3e50;outline:none;border-radius:15px;margin-right:10px;transition:border-color 0.2s ease;background:#fff url(data:image/png;base64,) 8px 5px no-repeat;background-size:20px;vertical-align:middle !important;margin-bottom:2em}.sidebar .search:focus{border-color:#9c27b0}.sidebar ul{list-style-type:none;margin:0;line-height:1.5em;padding-left:0}.sidebar ul li{position:relative;padding:5px 0px;padding-left:15px;margin-top:0.5em}.sidebar ul li ul{padding-left:1em;font-size:0.85em}.sidebar ul li .item{color:#000;text-decoration:none}.sidebar ul li .item:hover{border-bottom:2px solid #333}.sidebar ul li i{margin-right:10px}.sidebar ul .arrow{width:0;height:0;border-top:5px solid transparent;border-bottom:5px solid transparent;border-left:5px solid #d0d0d0;position:absolute;left:0;top:15px;transition:-webkit-transform 0.5s ease-out;transition:transform 0.5s ease-out;transition:transform 0.5s ease-out, -webkit-transform 0.5s ease-out}.sidebar ul .open .arrow{-webkit-transform:rotate(90deg);transform:rotate(90deg)}@media screen and (max-width: 900px){.sidebar{position:fixed;z-index:10;background-color:#f9f9f9;height:100%;top:20px;left:0;box-shadow:0 0 10px rgba(0,0,0,0.2);transition:all 0.4s cubic-bezier(0.4, 0, 0, 1);-webkit-transform:translate(-280px, 0);transform:translate(-280px, 0);overflow-y:auto}.sidebar.open{-webkit-transform:translate(0, 0);transform:translate(0, 0)}.sidebar .sidebar-inner{padding:50px 10px 10px 20px;box-sizing:border-box}.sidebar ul{display:block}}

*{font-family:"Montserrat", sans-serif}*:focus{outline:none}h1,h2,h3,h4,b,strong{font-family:"Baloo 2", cursive}body{color:#34495e;background:#fff;margin:0;padding-top:61px}.main{z-index:1;overflow-x:hidden;position:static}@media screen and (max-width: 900px){.main{padding:2em 1.4em 0}}.content{position:relative;padding:0 2em;margin:0 auto;margin-left:280px}@media screen and (max-width: 1300px){.content{margin-left:290px}}@media screen and (max-width: 900px){.content{margin:auto;padding:0}}.content .title{text-align:center;padding-top:20px;color:#000}.content .footer{color:#7f8c8d;margin-top:2em;padding-top:2em;border-top:1px solid #e5e5e5;font-size:0.9em}a{color:#9c27b0;text-decoration:none}.initial-appear{opacity:0.01}.initial-appear.initial-appear-active{opacity:1;transition:opacity 0.5s ease-in}.initial-enter{opacity:0.01}.initial-enter.initial-enter-active{opacity:1;transition:opacity 500ms ease-in}.initial-leave{opacity:1}.initial-leave.initial-leave-active{opacity:0.01;transition:opacity 300ms ease-in}.button{border:1px solid rgba(156,39,176,0.45);padding:0 10px;border-radius:20px;transition:all 0.3s ease-in;display:inline-block;color:#9c27b0}.button:hover{background-color:#9c27b0;color:#fff}

